import Image from 'next/image'
import { forwardRef, ReactNode } from 'react'

interface ResponsiveOverlayImageProps {
  src?: string
  alt: string
  mobileSrc?: string
  tabletSrc?: string
  desktopSrc?: string
  overlayOpacity?: string
  priority?: boolean
  className?: string
  overlayClassName?: string
  imageClassName?: string
  sizes?: string
  quality?: number
  placeholder?: 'blur' | 'empty' | 'data:image/...'
  blurDataURL?: string
  fill?: boolean
  width?: number
  height?: number
  aspectRatio?: string
  children?: ReactNode
  [key: string]: any
}

interface ImageSources {
  mobile: string
  tablet: string
  desktop: string
}

const ResponsiveOverlayImage = forwardRef<HTMLDivElement, ResponsiveOverlayImageProps>(({
  src,
  alt,
  mobileSrc,
  tabletSrc,
  desktopSrc,
  overlayOpacity = 'bg-black/50',
  priority = false,
  className = '',
  overlayClassName = '',
  imageClassName = '',
  sizes = '(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw',
  quality = 90,
  placeholder = 'blur',
  blurDataURL,
  fill = false,
  width,
  height,
  aspectRatio = 'aspect-[16/9]',
  children,
  ...props
}, ref) => {
  // Determine the appropriate image source based on screen size
  const getImageSources = (): ImageSources => {
    return {
      mobile: mobileSrc || src || '',
      tablet: tabletSrc || desktopSrc || src || '',
      desktop: desktopSrc || src || ''
    }
  }

  const imageSources: string | ImageSources = (desktopSrc || tabletSrc || mobileSrc)
    ? getImageSources()
    : src || ''

  return (
    <div
      ref={ref}
      className={`relative overflow-hidden ${aspectRatio} ${className}`}
      {...props}
    >
      {/* Responsive Images */}
      {typeof imageSources === 'object' ? (
        <>
          {/* Mobile Image */}
          <Image
            src={imageSources.mobile}
            alt={alt}
            fill={fill}
            width={!fill ? width : undefined}
            height={!fill ? height : undefined}
            priority={priority}
            quality={quality}
            placeholder={placeholder}
            blurDataURL={blurDataURL}
            sizes={sizes}
            className={`object-cover transition-transform duration-700 ease-out hover:scale-105 md:hidden ${imageClassName}`}
          />

          {/* Tablet Image */}
          <Image
            src={imageSources.tablet}
            alt={alt}
            fill={fill}
            width={!fill ? width : undefined}
            height={!fill ? height : undefined}
            priority={priority}
            quality={quality}
            placeholder={placeholder}
            blurDataURL={blurDataURL}
            sizes={sizes}
            className={`object-cover transition-transform duration-700 ease-out hover:scale-105 hidden md:block lg:hidden ${imageClassName}`}
          />

          {/* Desktop Image */}
          <Image
            src={imageSources.desktop}
            alt={alt}
            fill={fill}
            width={!fill ? width : undefined}
            height={!fill ? height : undefined}
            priority={priority}
            quality={quality}
            placeholder={placeholder}
            blurDataURL={blurDataURL}
            sizes={sizes}
            className={`object-cover transition-transform duration-700 ease-out hover:scale-105 hidden lg:block ${imageClassName}`}
          />
        </>
      ) : (
        /* Single Image */
        <Image
          src={imageSources}
          alt={alt}
          fill={fill}
          width={!fill ? width : undefined}
          height={!fill ? height : undefined}
          priority={priority}
          quality={quality}
          placeholder={placeholder}
          blurDataURL={blurDataURL}
          sizes={sizes}
          className={`object-cover transition-transform duration-700 ease-out hover:scale-105 ${imageClassName}`}
        />
      )}

      {/* Black Classic Overlay */}
      <div
        className={`absolute inset-0 ${overlayOpacity} transition-opacity duration-300 hover:bg-black/40 ${overlayClassName}`}
        aria-hidden="true"
      />

      {/* Content Overlay */}
      {children && (
        <div className="absolute inset-0 z-10 flex items-center justify-center p-4 sm:p-6 lg:p-8">
          <div className="text-center text-white">
            {children}
          </div>
        </div>
      )}
    </div>
  )
})

ResponsiveOverlayImage.displayName = 'ResponsiveOverlayImage'

export default ResponsiveOverlayImage

